*{
    margin: 0;
    padding: 0;
    text-decoration: none;
    outline: none;
}
body{
    width: 100%;
    background-color: #F0F2F5;
}
form{
    position: absolute;
    background-color: #ffff;
    width: 320px;
    height: 270px;
    padding: 10px 5px 20px 5px;
    margin: -10vh 850px;
    border-radius: 5px;
    box-shadow: 5px 5px 6px -7px;
}
input[type="text"],input[type="password"]{
    display: block;
    background-color: #fff;
    margin: auto;
    border: 1.5px solid rgb{};
}

